<template>
    <section class="projects-section" id="projects">
        <div class="desc">
            <p>PROJECTS</p>
        </div>
        
        <div class="projects">
            <article v-for="(project, index) in projects" :key="index">
                <div class="project-wrapper">
                    <div class="front">
                        <div class="project-body">
                            <h3>{{ project.title }}</h3>
                            <figure>
                                <img :src="project.image" alt="">
                            </figure>
                            <div class="read-more">
                                Hover to read more
                            </div>
                        </div>
                    </div>
                    <div class="back">
                        <ul>
                            <li v-for="(detail, d_id) in project.details" :key="d_id">
                                {{ detail }}
                            </li>
                        </ul>

                        <a :href="project.link" target="blank" v-if="project.link">
                            <span class="material-symbols-outlined">
                                link
                            </span>
                        </a>
                    </div>
                </div>
            </article>
        </div>
    </section>
</template>


<script>
    export default{
        name: 'Projects',
        data(){
            return{
                projects: [
                    {
                        title: 'Contacts Manager',
                        image: require('../assets/contacts-manager.svg'),
                        details: [
                            'Working on a Contact Manager Project',
                            'Utilizing Node.js and Express.js to handle API requests',
                            'CRUD functionalities to efficiently create, retrieve, update and delete contact records.',
                            'Leveraging MongoDB for data storage and retrieval of contact information',
                            'Front-end design and layout with Vue.js',
                        ],
                        link: 'https://github.com/ritikxsharma/contactManager'
                    },
                    {
                        title: 'TO-DO List',
                        image: require('../assets/todo.svg'),
                        details: [
                            'Developed web application for to-do lists',
                            'Implemented CRUD funcationalities to manage tasks, enabling users to add, view, edit and delete tasks',
                            'Front-end development using HTML, CSS and JavaScript.',
                        ],
                        link: 'https://github.com/ritikxsharma/Todo-Flask',
                    },
                    {
                        title: 'Intelligent Text Analyser (NLP)',
                        image: require('../assets/nlp.svg'),
                        details: [
                            'The project showcases the multifaceted capabilities of Natural Language Processing (NLP) and its potential to transform how we interact with textual data.',
                            'The project leveraged Natural Language Processing (NLP) functionalities to determine sentiment polarity, classify spam emails, extract key topics from text, generate a text summary.',
                            'Also created a web application for the project using Flask.',
                        ],
                        link: null
                    }
                ]
            }
        }
    }
</script>

<style lang="scss" scoped>

    @import '../../mixins.scss';

    .projects-section {
        position: relative;
        width: 100%;
        min-height: 100vh;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 0 1rem;

        .desc{
            position: relative;
            font-size: 2rem;
            text-align: center;
            margin-top: 5rem;
        }

        .projects{
            position: relative;
            width: 100%;
            display: flex;
            justify-content: space-around;
            flex-wrap: wrap;
            gap: 20px;

            article {
                width: 320px;
                height: 400px;
                margin: 0.5rem;
                padding: 0 0.5rem;
                background: #081b29;
                perspective: 1000px;
                transition: all 1s ease-in-out;
                font-size: 1rem;

                @include respond(large){
                    width: 250px;
                    height: 320px;
                    font-size: 0.8rem;
                }

                @include respond(medium){
                    width: 200px;
                    height: 250px;
                }

                .project-wrapper{
                    width: 100%;
                    height: 100%;
                    transition: transform 0.8s;
                    transform-style: preserve-3d;

                    .front{
                        position: absolute;
                        display: flex;  
                        flex-direction: column;
                        align-items: center;
                        text-align: center;
                        width: 100%;
                        height: 100%;
                        padding: 0.8rem 1rem;
                        color: #fff;
                        backface-visibility: hidden;

                        .project-body{
                            position: absolute;
                            width: 100%;
                            height: 100%;
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            text-align: center;

                            figure{
                                width: 60%;
                                height: 50%;
                                img{    
                                    width: 70%;
                                    height: 100%;
                                    filter: invert(64%) sepia(68%) saturate(4860%) hue-rotate(164deg) brightness(98%) contrast(101%);
                                }
                            }
                        }
                    }
                    .back{
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        padding: 1rem;
                        color: #fff;
                        transform: rotateY(180deg);
                        backface-visibility: hidden;
                        text-align: left;

                        @include respond(medium){
                            padding: 1rem;
                            font-size: .65rem;
                        }

                        a{
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            text-decoration: none;
                            color: #00abf0;
                            
                            span{
                                font-size: 1.5rem;
                            }
                        }
                    }
                }

                &:hover .project-wrapper{
                    transform: rotateY(-180deg);
                }

                &:hover{
                    transform: scale(1.02);
                    overflow: hidden;
                }
            }
        }
    }

</style>